@tailwind base;
@tailwind components;
@tailwind utilities;

/* --- Final Google Translate Widget CSS Fixes --- */

/* Hides the Google Translate banner that appears at the top */
.goog-te-banner-frame.skiptranslate {
    display: none !important;
} 

/* Prevents the page content from being pushed down by the banner */
body { 
    top: 0px !important; 
}

/* Hides the "Powered by" text, which is inside a now-empty div */
#google_translate_element .goog-te-gadget-simple > span {
    display: none;
}

/* Hides the Google logo image */
#google_translate_element .goog-te-gadget-simple img {
    display: none !important;
}

/* Styles the actual dropdown to match your UI */
select.goog-te-combo {
    /* Match the select from your Header */
    width: 7rem; /* w-28 */
    background-color: transparent !important;
    border: 1px solid #4B5563; /* border-gray-600 */
    border-radius: 0.375rem; /* rounded-md */
    padding: 0.5rem;
    font-size: 0.75rem; /* text-xs */
    color: #D1D5DB; /* dark:text-gray-300 */
    cursor: pointer;
}

/* Styles the dropdown text color for light mode */
html:not(.dark) select.goog-te-combo {
    color: #374151; /* text-gray-700 */
    border: 1px solid #D1D5DB; /* border-gray-300 */
}
